<template>
  <el-button 
  :type="type"
  :icon="icon" 
  @click="sendRequest">
    <slot v-if="!clicked"></slot>
    <span v-if="clicked">{{clickedMsg}}</span>
  </el-button>
</template>

<script>
import request from "superagent";   
const stringProp = (propName) => {
    return {
        type: String, 
        default: '',
    }
};
export default {
  name: "request-button",
  props: {
      type: stringProp('type'), 
      icon: stringProp('icon'), 
      clicked: {
          type: Boolean, 
          default: false,
      },
      clickedMsg: stringProp('clickedMsg'), 
      requestUrl: stringProp('requestUrl'),
  },
  methods: {
    sendRequest() {
      request
        .get(this.requestUrl)
        .then(resp => {
          this.$message && this.$message(resp.body.message);
          this.clicked = !this.clicked;
        })
        .catch(err => {
          this.$message && this.$message(err.body.message);
        });
    }
  }
};
</script>

