<template>
<el-row :gutter="picProfileGutter">
    <el-col  :span="6">
        <img :src="user.avator_url" alt="" :height="avatorHeight + 'px'">
    </el-col>
    <el-col :span="6" >
        <el-form label-position="left" inline>
            <el-form-item label="">
                <span v-text="user.nickname"></span>
            </el-form-item>
            <el-form-item label="" >
                <span v-text="user.username"></span>
            </el-form-item>
            <el-form-item label="" >
                <span v-text="user.email"></span>
            </el-form-item>
            <el-form-item label="" >
                <span v-text="user.school"></span>
            </el-form-item>
            <el-form-item label="" >
                <span v-text="user.borrowed_book_nums"></span>
            </el-form-item>
        </el-form>
    </el-col>
</el-row>
    
</template>

<script>
import request from 'superagent';

export default {
    name: 'userprofile-view',
    props:{
        requestUrl: {
            type: String, default: ''
        }, 
        picProfileGutter: {
            type: Number, default: 2
        }, 
        avatorHeight: {
            type: Number, default: 200
        }
    }, 
    data() {
        return {
            user: {
                avator_url: undefined, 
                nickname: undefined, 
                username: undefined, 
                email: undefined, 
                school: undefined, 
                borrowed_book_num: undefined,

            }
        }
    }, 
    created(){
        request.get(this.$props.requestUrl)
        .then(res=>{
            this.user = Object.assign({}, this.user, res.body.data);
        })
        .catch(err=>{
            this.$message && this.$message(err.message);
        });
    }, 
}
</script>
