<template>
  <el-dialog 
  @close="close"
  :visible="dlgVisible" 
  :title="">
    <el-table :data="userbookData">
      <el-table-column prop="deprecation" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <a :href="scope.row.user.profile_url" v-text="scope.row.user.nickname"></a>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-rate v-if="scope.row.avg_rate
                 !== null" :value="scope.row.avg_rate">
            <span>{{scope.row.rate_num}} </span>
          </el-rate>
          <span v-else></span>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button size="mini" @click="() => selectUserbook(scope.row)"></el-button>
        </template>
      </el-table-column>
      <el-table-column type="expand">
        <template slot-scope="scope">
          <el-row>
            <el-col
              v-for="(url, index) in scope.row.img_urls"
              :key="url"
              :span="3"
            >
              <a @click="() => openImgDlg(scope.row.img_urls, index)"><img :src="url" alt height="50px"></a>
            </el-col>
          </el-row>
        </template>
      </el-table-column>
    </el-table>
  </el-dialog>
</template>

<script>
import request from "superagent";
export default {
  name: "userbook-list-dlg",
  props: {
    dlgVisible: {
      type: Boolean,
      default: false
    },
    requestUrl: {
      type: String,
      required: true
    },
    selectedUserbook: {
      type: Object,
      required: true
    },
    imgs2show: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      userbookData: [],
      visible: false
    };
  },
  created() {
    request
      .get(this.requestUrl)
      .then(resp => {
        this.userbookData = resp.body.data;
      })
      .catch(err => {
        this.$message && this.$message(err.message);
      });
  },
  methods: {
    selectUserbook(userbook) {
      this.$emit("update:selectedUserbook", userbook);
      console.log(userbook);
      this.close();
    },
    openImgDlg(urls, index) {
      this.$emit("update:imgs2show", { urls: urls, index: index });
    },
    close() {
      this.handleDlg(false);
    },
    handleDlg(isOpen) {
      this.$emit("update:dlgVisible", isOpen);
    }
  },
};
</script>

