import addressMap from 'region-picker/dist/data.json'

let callTime = 0;
export const getPlaceNameByCode = (code) => {
    console.log(code);
    name =  findPlaceByName(addressMap, code, '');
    return name;
}
function findPlaceByName(map, code, prefix) {
    if (map['adcode'] === code) {
        return prefix+map['name'];
    } else {
        if (map['districts']) {
            for (var district of map['districts']) {
                let name = findPlaceByName(district, code, prefix+map['name']);
                if(name !== null){
                    return name;
                }
            }
            return null;
        }else{
            return null;
        }
    }
};