import PostbookForm from './postbook-form';
import UserSpaceTable from '../src/views/UserSpaceTable.vue';
import UserProfileView from '../src/views/UserProfileView.vue';
import RequestButton from '../src/components/RequestButton.vue';
import RegionPicker from '../src/components/RegionPicker.vue';
import UserBookListDlg from '../src/components/UserbookListDlg.vue';
import UserBookImgDlg from '../src/components/UserbookImgDlg.vue';
import UserBookView from '../src/views/UserbookView.vue';
import ElementUI from 'element-ui';

const components = [
    PostbookForm,
    ElementUI,
    UserSpaceTable,
    UserProfileView,
    RequestButton,
    RegionPicker,
    UserBookListDlg,
    UserBookImgDlg, 
    UserBookView,
]

const install = function (Vue) {
    if (install.installed) {
        return;
    }
    components.forEach(
        component => {
            // registering components
            component.name && Vue.component(component.name, component);
        }
    );
}

if (typeof window !== undefined && window.Vue) {
    install(window.Vue);
}

export default {
    install,
    PostbookForm,
    UserSpaceTable,
    UserProfileView
}