<template>
    <region-picker_ 
    v-model="regionVal"
    :data="regionMeta"></region-picker_>
</template>


<script>
import RegionPicker from 'region-picker';
import data from 'region-picker/dist/data.json';
import 'region-picker/dist/region-picker.css';

export default {
    name: 'region-picker', 
    data() {
        return {
            regionMeta: data, 
            regionVal: undefined, 
        }
    },
    components: {
        'region-picker_': RegionPicker, 
    }, 
    watch: {
        regionVal: function (newVal, oldVal) {
            this.$emit('change', newVal);
            this.$emit('input', newVal);
        }
    }
}
</script>
