<template>
  <el-row>
      <el-row>
          <span 
          v-if="daterangeLabel"
          v-text="daterangeLabel + ': '"></span>
          <el-date-picker
          v-if="daterangeProp"
          type="daterange"
          v-model="daterangeVal"
          start-placeholde=""
          end-placeholde=""
          range-separator="-"
          @change="onChange"></el-date-picker>
      </el-row>
    <el-table :data="displayData">
      <slot></slot>
    </el-table>
  </el-row>
</template>


<script>
import request from "superagent";
export default {
  name: "userspace-table",
  props: {
    requestUrl: {
      type: String,
      default: ""
    }, 
    daterangeProp: {
        type: String, 
        default: "",
    }, 
    daterangeLabel: {
        type: String, 
        default: ''
    }
  },
  data() {
    return {
      tableData: [], 
      daterangeVal: '',
    };
  },
  created() {
    request
      .get(`${this.$props.requestUrl}`)
      .then(res => {
        this.$data.tableData = res.body.data;
      })
      .catch(error => {
        this.$message && this.$message(error.message);
      });
  }, 
  computed: {
      isFiltering(){
          return this.daterangeVal;
      }, 
      displayData(){
          if(this.isFiltering){
          let [start, end] = this.daterangeVal;
          return this.tableData.filter(
              (data) => {
                  return start<= new Date(data[this.$props.daterangeProp]) &&
                        new Date(data[this.$props.daterangeProp]) <=end;
              }
          );
          }else{
              return this.tableData;
          }
      }
  }, 
  methods: {
      onChange(daterange){
          this.daterangeVal && console.log(this.daterangeVal);
      }
  }
};
</script>

