<template>
  <el-form :model="form" ref="form" :rules="rules"  label-position="left">
    <el-form-item label="ISBN" prop="isbn">
      <el-col :span="10">
        <el-autocomplete
          v-model="form.isbn"
          placeholder="ISBN"
          @select="onSelect"
          :fetch-suggestions="(val, cb) => searchBook(val, 5, cb)"
        >
          <template slot-scope="{item}">
            <img :src="item.images.small.replace('\\', '')" height="100px">
            <div v-text="item.title" class="title"></div>
            <span v-text="item.isbn13 || item.isbn10" class="isbn"></span>
          </template>
        </el-autocomplete>
      </el-col>
    </el-form-item>
    <el-form-item label="">
      <el-col :span="6">
        <el-button
          @click="showTestDialog = true"
          v-text="deprecationRate>0? `Redo`: `Do the test`"
          class="testBtn"
        ></el-button>
        <span
          class="deprecation-box"
          v-if="deprecationRate > 0"
          v-text="`Points:${deprecationRate}`"
        ></span>
      </el-col>
    </el-form-item>
    <el-dialog :visible.sync="showTestDialog" title="">
      <el-form :model="testForm" ref="testForm" label-position="top">
        <el-form-item 
        v-for="q in testForm.questions" 
        :key="q.id" :label="q.label"
        :rules="[{required: true, message: '', trigger: 'blur'}]">
          <el-select v-model="q.ans" placeholder="">
            <el-option
              :label="choice.description"
              :key="choice.id"
              :value="choice.value * q.weight"
              v-for="choice in q.choices"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-button @click="showTestDialog=false"></el-button>
      </el-form>
    </el-dialog>
    <el-form-item label="" prop="photoIds">
      <el-upload
        ref="upload"
        name="photos"
        :auto-upload="false"
        :action="uploadImgUrl"
        list-type="picture"
        :before-upload="onBeforeUpload"
        :on-remove="removePic"
        :on-success="onUploadSuccess"
      >
        <el-button slot="trigger"></el-button>
        <el-button
         @click="handleUpload"></el-button>
        <slot name="csrf_token">CSRF</slot>
      </el-upload>
    </el-form-item>
    <el-form-item label="" prop="description">
      <el-input 
      type="textarea"
      rows="3"
      maxlength="50" 
      v-model="form.description"
      ></el-input>
    </el-form-item >
    <user-addresslist-select 
    v-model="form.addressId"
    label=""
    :userAddressList="userInfo.address"
    formProp="addressId">
    </user-addresslist-select>
    <el-form-item>
      <el-button 
      @click="onSubmitForm"
      type="primary"></el-button>
    </el-form-item>
  </el-form>
</template>


<script>
import request from "superagent";
import jsonp from "superagent-jsonp";
import {getPlaceNameByCode} from '../../../src/components/utils.js';
import { uploadImgUrl as uploadUrl } from "../../../src/settings/constants.js";
import UserAddressSelect from "components/UserAddressSelect.vue";
const apiUrl = "https://api.douban.com/v2/book";
const searchBook = (keyword, count, cb) => {
  // console.log(`${apiUrl}/search?q=${keyword}&count=${count}`);
  request
    .get(`${apiUrl}/search?q=${keyword}&count=${count}`)
    .use(jsonp({ timeout: 1000 }))
    .end((err, res) => {
      if (!err) {
        cb && cb(res.body.books);
      }
    });
};
export default {
  name: "postbook-form",
  components: {[UserAddressSelect.name]: UserAddressSelect},
  props: {
    uploadImgUrl: {
      type: String,
      default: "no post url"
    }, 
    submitFormUrl: {
      type: String, 
      default: ""
    }, 
    redirectUrl: {
      type: String, 
      default: ""
    }, 
    userInfoUrl: {
      type: String, 
      required: true,
    }, 
  },
  data() {
    let idCount = 0;
    const makePctChoice = (description, val) => {
      return {
        id: idCount++,
        description: description,
        value: val
      };
    };
    return {
      showTestDialog: false,
      userInfo: {},
      currentBookImgSrc: undefined,
      defaultUploadImgUrl: this.uploadImgUrl,
      photoIds: [],
      idCount: 0,
      form: {
        deprecation: undefined,
        isbn: "",
        description: "",
        addressId: null, 
        photoIds: [], 
        book: {
          name: '', 
          author: '',
          img_src: '',
          douban_id: undefined, 
          publisher: '', 
          pub_date: '',
        }
      },
      testForm: {
        questions: [
          {
            id: idCount++,
            label: "",
            choices: [
              makePctChoice("", 0.9),
              makePctChoice("", 0.7),
              makePctChoice("", 0.5)
            ],
            ans: undefined,
            weight: 0.3
          },
          {
            id: idCount++,
            label:
              "",
            choices: [
              makePctChoice("", 0.8),
              makePctChoice("", 0.6),
              makePctChoice("", 0.5),
              makePctChoice("", 1)
            ],
            ans: undefined,
            weight: 0.7
          }
        ]
      }, 
      rules: {
        deprecation: [
          {type: 'number', min: 1, max: 100, message: ''}
        ], 
        isbn: [
          {validator: (rule, val, cb) => {
            if(!val){
              cb(new Error('ISBN'));
            } else if(!(/^(\d{11}|\d{13})$/.test(val))){
              cb(new Error('ISBN '));
            } else{
              cb();
            }
          }, trigger: ['blur', 'change']}, 
        ],
        addressId: [
          {type: 'number', required:true, message: ''}
        ],
        photoIds: [
          {type: 'array', required: true, message: ''}
        ], 
        description: [
          {
            required: true, message: '', trigger: 'blur'
          }
        ]
      }
    };
  },
  computed: {
    deprecationRate() {
      let score = 0;
      for (var q of this.testForm.questions) {
        score += q.ans;
      }
      // console.log(score);
      return (score * 100).toFixed(2);
    }
  },
  methods: {
    searchBook(kw, count, cb) {
      return kw && searchBook(kw, count || 5, cb);
    },
    onSelect(item) {
      this.form.isbn = item.isbn13 || item.isbn10;
      this.form.book.name = item.title;
      this.form.book.other_name = item.origin_title;
      this.form.book.author = item.author;
      this.form.book.douban_id = item.id;
      this.form.book.publisher = item.publisher;
      this.form.book.pub_date = item.pubdate;
      this.form.book.img_src = item.image.replace('\\', '');
    },
    removePic(file, fileList) {
      
      if(file.response && file.response.photoId){
      this.photoIds = this.photoIds.filter(
        (id) => id !== file.response.photoId);
      console.log(file, fileList);
      console.log(this.photoIds);
      } 
    },
    handleUpload() {
      this.$refs.upload.submit();
    },
    onUploadSuccess(resp, file, fileList) {
      this.photoIds.push(resp.photoId);
      console.log(this.form);
      console.log(this.photoIds);
    },
    onSubmitForm() {
      this.form.photoIds = this.photoIds;
      this.form.deprecation = this.deprecationRate;


      this.$refs["form"].validate(valid => {
        if(valid){
          this.submitFormUrl &&
           request.post(this.submitFormUrl)
           .send(this.form)
           .then(
             resp => {
              this.$confirm('', '', 
              {
                confirmButtonText: '', 
                cancelButtonText: '', 
                distinguishCancelAndClose: true,
              }).then( 
                () => {
                  this.resetForm();
                }
              ).catch(action => {
                if(action === 'cancel'){
                  document.location.href = this.redirectUrl;
                }
              });
             }
           ).catch(
             (err) => {
               err.message && this.$message.error(err.message);
             }
           );
        }else {
          this.$message.warning("");
        }
      });
    }, 
    resetForm(){
      this.$refs['upload'].clearFiles();
      this.photoIds = [];
      this.testForm.questions.forEach((q, i)=>{
        q.ans = undefined;
      });
      this.form.isbn = '';
      this.form.description = '';
      this.form.photoIds = [];
    }, 
    onBeforeUpload(file) {
      console.log('triggered');
      let isImage = file.type.startsWith('image');
      let allowedSize = file.size / 1024 / 1024 <= 3;
      isImage || this.$message('');
      allowedSize || this.$message('3M');

      return isImage && allowedSize;
    }
  }, 
  created() {
    request.get(this.userInfoUrl).then(
      (resp) => {
        this.userInfo = resp.body.data;
      }
    ).catch(
      err => {
        this.$message && this.$message(err.message);
      }
    );
  }
};
</script>

<style lang="scss">
.deprecation-box {
  margin-left: 10px;
}

.isbn {
  color: silver;
  font-size: 1em;
}
</style>
